footer.footer {
  padding: calc($spacing*2) 0 $spacing;
  font-size: $fs-15;

  @include bp-m {text-align: left;}

  body.analytics & {display: none;}

  a {
    color: $clr-1;
    text-decoration-line: none;

    @include bp-m {
      &:hover,
      &:focus {
        text-decoration-line: underline;
        color: $clr-b1;
      }
    }
  }

  // site

  div.site {
    body.landing & {@include hide-screen();}

    div.newsletter {
      margin-bottom: $vmargin;

      p {
        margin-bottom: 0.25rem;
        font-size: $fs-18;
        font-weight: 700;
      }
    }

    div.links {
      display: flex;
      flex-wrap: wrap;
      margin: 0 (-$gutter);

      @include bp-m {flex-wrap: nowrap;}

      dl {
        flex: 1 1 auto;
        width: 50%;

        @include bp-s-less {
          &:nth-of-type(odd) {padding: 0 calc($gutter/2) 0 ($gutter);}
          &:nth-of-type(even) {padding: 0 ($gutter) 0 calc($gutter/2);}
        }

        @include bp-s {
          flex: 1;
          padding: 0 calc($gutter/2);
          width: 25%;

          &:first-child {padding-left: $gutter;}
          &:last-child {padding-right: $gutter;}
        }

        @include bp-m {
          width: auto;
        }

        dt {
          margin-bottom: 1rem;
          font-size: $fs-18;
          font-weight: 700;
        }

        dd {
          margin: 0.6rem 0;
          line-height: 1.2;
        }
      }
    }
  }

  // company

  div.company {
    text-align: center;

    @include bp-m {
      align-items: end;
      margin-top: -2rem;

      body.landing & {margin-top: 0;}

      > div.col:first-child {text-align: left;}
      > div.col:last-child {text-align: right;}
    }

    h2 {
      display: inline-block;
      margin: 0 0 0.25rem;
      width: 100%;
      max-width: 265px;
      background: url(../../assets/images/logos/mantra-systems.svg) no-repeat 50% 100%; 
      background-size: 100% auto;

      &:before {
        display: block;
        padding-top: 12%;
        content: "";
      }

      span {@include hide-screen();}
    }

    ul.contact-buttons {
      @include bp-m {margin-bottom: calc($vmargin/1.5);}
      body.landing & {@include hide-screen();}

      li {
        display: inline-block;

        a {
          border-radius: 0.25rem;
          padding: 0.5rem;

          &:before {
            margin: 0;
            font-size: $fs-25;
          }
          
          span {@include hide-screen();}
        }
      }
    }

    p.location {
      margin: 0;
      b {display: block;}
    }

    p.info {margin: 0;}

    ul.registration-logos {
      margin: 0 0 calc($vmargin/2);
      text-align: inherit;

      @include bp-m {margin: 0 0 0 -1rem;}

      li {
        display: inline-block;
        margin: 0.5rem 0;

        @include bp-s {margin: 0.5rem;}
        @include bp-m {margin: 0.5rem 0 0 1rem;}

        figure {
          max-width: none;

          img {
            width: auto;
            max-width: 100%;
            max-height: 70px;
          }
        }
      }
    }
  }
}
