// background themes

$bgDarkGrad: linear-gradient(to bottom, rgba(black,0.35) 0%, transparent 25%, transparent 75%, rgba(black,0.35) 100%);
$hexagonsDark: url(../../assets/images/content/patterns/hexagons-dark.svg);

.bg-b0,
.bg-1,
.bg-b2,
.bg-b3,
.bg-b4,
.bg-b6 {
  background-size:
    auto,
    auto,
    173px auto;
}

.bg-dark {color: white;}
.bg-1 {background-color: $clr-1;}
.bg-b5 {background-color: $clr-b5;}
.bg-b7 {background-color: $clr-b7;}

.bg-b5,
.bg-b7 {
  background-image:
    linear-gradient(to right, rgba(white,0.2) 0%, rgba(white,0.2) 100%),
    url(../../assets/images/content/patterns/hexagons.svg);
  background-size:
    cover,
    173px auto;
}

.bg-b0 {
  background-color: $clr-b0;
  background-image: 
    $bgDarkGrad,
    linear-gradient(to bottom, rgba($clr-b0,0.1) 0%, $clr-b0 35%, $clr-b0 65%, rgba($clr-b0,0.1) 100%),
    $hexagonsDark;
}

.bg-1 {
  background-color: $clr-1;
  background-image: 
    $bgDarkGrad,
    linear-gradient(to bottom, rgba(black,0.6) 0%, rgba(black,0.4) 100%),
    $hexagonsDark;
}

.bg-b2 {
  background-color: $clr-b2;
  background-image: 
    $bgDarkGrad,
    linear-gradient(to bottom, rgba($clr-b2,0.1) 0%, $clr-b2 35%, $clr-b2 65%, rgba($clr-b2,0.1) 100%),
    $hexagonsDark;
}

.bg-b3 {
  background-color: $clr-b3;
  background-image: 
    $bgDarkGrad,
    linear-gradient(to bottom, rgba($clr-b3,0.1) 0%, $clr-b3 35%, $clr-b3 65%, rgba($clr-b3,0.1) 100%),
    $hexagonsDark;
}

.bg-b4 {
  background-color: $clr-b4;
  background-image: 
    $bgDarkGrad,
    linear-gradient(to bottom, rgba($clr-b4,0.1) 0%, $clr-b4 35%, $clr-b4 65%, rgba($clr-b4,0.1) 100%),
    $hexagonsDark;
}

.bg-b6 {
  background-color: $clr-b6;
  background-image: 
    $bgDarkGrad,
    linear-gradient(to bottom, rgba($clr-b6,0.1) 0%, $clr-b6 35%, $clr-b6 65%, rgba($clr-b6,0.1) 100%),
    $hexagonsDark;
}

.bg-ci {
  background-color: #4801AF;
  background-image:
    linear-gradient(to right, rgba(#4801AF,0.75) 0%, rgba(#006C9D,0.75) 100%),
    url(../../assets/images/content/patterns/waves.svg);
  background-size: cover;
  background-position: 50%;
}

// strip

div.strip {
  position: relative;
  padding: calc($spacing*1.5) 0;
  @include bp-m {padding: calc($spacing*2) 0;}

  &.section {
    padding: calc($spacing*2) 0 calc($spacing*1);

    @include bp-m {
      padding: calc($spacing*3) 0 calc($spacing*2);
      &.latest p.lead span {display: block;}
    }

    div.wrap > h2 {@extend .h1;}

    &[class*="bg-dark"] {
      box-shadow: inset 0 1px 0 0 rgba(white,0.1), inset 0 -1px 0 0 rgba(white,0.1);
      div.wrap > h2 {font-weight: 200;}
      p.lead a {color: white;}
    }
  }

  &.core {@include bp-m-less {padding: 0;}}
  &.alt {padding-top: 0;}

  // contact 

  &.contact {
    @include bp-m {text-align: left;}
  }

  // newsletter

  &.section.newsletter {
    padding: calc($spacing*1.5) 0;
    p {margin-bottom: 0.75rem;}
    div.form-group {margin: 0.6rem 0;}
  }

  // team

  &.team {
    h3 {@extend .h2;}
  }
}

// page intro

div.page-intro {
  position: relative;
  z-index: 2;
  padding: calc($spacing*4) 0 calc($spacing*2);
  text-align: center;

  @include bp-m {padding: calc($spacing*5.5) 0 calc($spacing*2.5);}

  body.analytics & {display: none;}
  body.guide & {@include bp-m {padding-bottom: $spacing*3;}}

  body.form-response &,
  body.article & {
    @include bp-m {padding: calc($vmargin*5) 0 calc($vmargin*3);}
    @include bp-l {padding: calc($vmargin*6) 0 calc($vmargin*4);}
  }

  body.article & {
    border-bottom: 1px solid white;

    div.aesthetic {
      position: absolute;
      top: 0; left: 0; bottom: 0; right: 0;
      z-index: 1;
      background-position: 50%;
      background-size: cover;
      opacity: 0.15;
      content: "";
    }

    div.wrap {
      position: relative;
      z-index: 2;
    }
  }

  &.product-summary {
    @include bp-m {
      padding: calc($spacing*6.5) 0 calc($spacing*2.5);
    }
  }

  div.wrap {
    > div.row {align-items: center;}
    > :last-child {margin-bottom: 0;}
  }

  figure {margin: $vmargin auto;}

  // overview - left side

  div.overview {
    @include bp-m {
      text-align: left;
      
      > :last-child {margin-bottom: 0;}
    }

    h1 {
      body.toolkit &,
      body.services & {
        font-size: $fs-34;
        @include bp-m {
          font-size: $fs-50;
          &.small {font-size: $fs-45;}
        }
      }
    }

    p.lead {
      //margin-bottom: $vmargin;
      max-width: none;
      span {display: inline;}

      @include bp-l {
        span {display: inline-block;}
      }
    }

    p.cta {
      > span {
        display: flex;
        align-items: center;
        margin: 1rem 0 0 2.75rem;
        font-size: $fs-17;

        @include bp-m-less {@include hide-screen();}

        @include bp-m {
          display: inline-flex;
          margin: 1rem;
        }

        &:before {
          order: 2;
          margin-left: 0.25rem;
          font-size: $fs-20;
          color: $clr-b1;
        }
      }
    }
  }

  // circle images - right side

  div.image-space {
    figure {
      overflow: hidden;
      position: relative;
      margin: 0 auto;
      border-radius: 50%;
      max-width: 250px;
      box-shadow: 0 0 50px 10px rgba(black,0.25);

      @include bp-m {margin-bottom: -7rem;}

      body.toolkit & {
        @include bp-m {
          max-width: 450px;
          margin-bottom: -6rem;
        }
      }

      &:after {
        position: absolute;
        top: 0; left: 0; bottom: 0; right: 0;
        z-index: 2;
        border-radius: 50%;
        content: "";
        box-shadow: inset 0 0 0 4px white, inset 0 0 25px 15px rgba(black,0.30);

        @include bp-m {box-shadow: inset 0 0 0 8px white, inset 0 0 25px 15px rgba(black,0.30);}
      }

      img {
        position: relative;
        z-index: 1;
        border: 1px solid white;
        border-radius: 50%;
      }
    }
  }

  // lead capture - right side - image + email + phone

  div.lead-capture {
    position: relative;

    div.cta-box {
      @include bp-m-less {
        background: rgba(black,0.2);
        color: white;
        box-shadow: none;
      }

      @include bp-m {
        margin-bottom: -6rem;
        body.toolkit & {margin-bottom: 0;}
        body.contact & {background: white;}
      }

      figure {
        margin: 0;

        img {
          display: block;
          width: 100%;
          height: auto;
        }
      }

      p {
        margin: 0 0 $vmargin;
        font-size: $fs-20;
        text-align: center;
       
        b {
          display: block;
          font-size: $fs-24;
        }
      }

      &.opportunities {
        text-align: left;
        font-size: $fs-17;
      
        @include bp-m {
          border-left: 30px solid $clr-warning;
          background: $clr-4;
        }

        dl {
          dt {margin-top: 0.5rem; font-size: $fs-17;}
          dd {padding-left: 1rem;}
        }
      }
    }
  }

  // price summary - right side - EnableCE

  div.price-summary {
    div.cta-box {
      overflow: hidden;
      //@include bp-m {margin-bottom: -8rem;}
    }
  }

  // CTA box - right side 

  div.cta-box {
    position: relative;
    z-index: 4;
    overflow: hidden;
    border-radius: $border-radius;
    background: $clr-4;
    color: $clr-1;
    box-shadow: 0 0 50px 10px rgba(black,0.25);

    p.header {
      margin: 0;
      padding: 0.5rem;
      background: $clr-warning;
      font-size: $fs-16;

      @include bp-m {
        b {display: inline-block;}
      }
    }

    h3.packages {
      margin: 0;
      padding: 0.4rem 0.5rem;
      background: $clr-3;
      font-size: $fs-16;
      font-weight: 400;
    }

    div.content {
      padding: $spacing;

      &.packages {
        padding: calc($spacing/2) $spacing;
        background: $clr-4;
      }

      > :last-child {margin-bottom: 0;}

      h2 {
        margin-bottom: 0.75rem;
        font-size: $fs-20;
        font-weight: 400;     
      }

      .asterisk {
        display: inline-block;
        text-align: left;
        font-size: $fs-16;

        li {
          margin: 0.2rem 0;
          &:before {font-size: $fs-14;}

          span.price {
            font-weight: 700;
            color: $clr-b6;

            &.separate {
              position: relative;
              overflow: hidden;
              opacity: 0.6;
              color: $clr-1;

              &:after {
                position: absolute;
                left: 0; right: 0; top: 50%;
                height: 1px;
                background: $clr-b1;
                transform: rotate(-10deg);
                content: "";
              }
            }
          }

          span.frequency {font-size: $fs-14;}
        }
      }

      p.continue {
        margin-top: -0.75rem;
        font-size: $fs-16;
      }
    }

    div.general-wrap {margin: 0;}
  }





// needs tidying


  h1 {
    margin-bottom: 0;

    body.home & {
      @include bp-l {font-size: $fs-60;}
    }

    body.error & {
      @extend .h2;
      span {
        display: block;
        font-size: $fs-70;
        @include bp-m {font-size: $fs-100;}
      }
    }

    &.toolkit {
      span.heading {
        display: block;

        @include bp-l {
          > span {display: block;}
        }
        
        body.toolkit-home & {
          font-size: $fs-45;
          @include bp-m {font-size: $fs-75;}
          @include bp-l {font-size: $fs-100;}

          b {font-weight: 400;}

          sup {
            font-weight: 400;
            font-size: $fs-18;
            line-height: 1.0;
            vertical-align: top;

            @include bp-m {line-height: 1.5; font-size: $fs-20;}
            @include bp-l {line-height: 2.0; font-size: $fs-22;}
          }
        }
      }
    }
  }

  p {

    &.lead {
      margin-top: $vmargin;

      @include bp-m {
        margin-left: auto;
        margin-right: auto;
        max-width: 800px;
      }

      div.home-intro & {
        margin-bottom: 0;
        @include bp-m {margin-bottom: $vmargin*2;}
      }

      body.toolkit-home &,
      div.home-intro & {
        @include bp-m {font-size: $fs-35; font-weight: 200;}
        @include bp-l {font-size: $fs-40;}
      }

      span {@include bp-m {display: block;}}
    }
  }

  &.bg-dark {
    p.lead a {color: white;}

    blockquote {
      p.quote {
        background: rgba(black,0.25);
        &:after {border-color: rgba(black,0.25) transparent transparent rgba(black,0.25);}
      }

      p.source {
        margin-bottom: 0;
        img {box-shadow: 0 0 0 6px rgba(black,0.25);}
      }
    }
  }
      
  ul.buttons {
    margin: $vmargin auto 0;
  }

  blockquote {
    position: relative;
    margin: $vmargin auto 0;
    
    @include bp-m {display: inline-block;}

    div.home-intro & {
      @include bp-l {
        //display: inline-block;
        margin: 0 0 calc($vmargin*2);
      }
    }

    p.quote {
      @include lead();
      position: relative;
      
      border-radius: $border-radius;
      padding: $spacing;
      background: rgba($clr-2,0.075);

      @include bp-l {
        margin: 0;
        font-size: $fs-30;
      }

      &:after {
        position: absolute;
        top: 100%; left: 50%;
        border: 10px solid;
        border-color: rgba($clr-2,0.075) transparent transparent rgba($clr-2,0.075);
        content: "";

        @include bp-l {
          left: auto; top: 50%; left: 100%;
          margin-right: -10px;
        }
      }

      > span {
        display: inline-block;
        span {@include bp-m {display: block;}}
      }
    }

    p.source {
      margin-left: auto;
      margin-right: auto;
      width: 4em;
      height: 4em;

      @include bp-l {
        position: absolute;
        left: 100%; top: 50%;
        margin: -2em 0 0 30px;
      }

      @include bp-l {font-size: $fs-25;}

      img {
        display: block;
        overflow: hidden;
        border-radius: 50%;
        width: 100%;      
        transform: scaleX(-1);
        box-shadow: 0 0 0 6px rgba($clr-2,0.075);
      }
    }
  }
}

// home intro

div.home-intro {
  input#question-funnel-1:checked ~ div.page-intro {@extend .bg-b2;}
  input#question-funnel-2:checked ~ div.page-intro {@extend .bg-b6;}
  input#question-funnel-3:checked ~ div.page-intro {@extend .bg-b3;}

  div.page-intro {
    position: relative;
    padding-bottom: 0;
    //transition: background 1s 0s;

    div.wrap {
      position: relative;
      z-index: 3;
    }

    h1 {
      position: relative;
      z-index: 10;
      margin-bottom: $vmargin;

      @include bp-s {margin-bottom: $vmargin*1.5;}
      @include bp-m {margin-bottom: $vmargin*2;}
      @include bp-l {margin-bottom: $vmargin*1.5;}

      span {display: block;}
    }
  }
}

// question funnels - home-intro

div.question-funnels {
  
  input[type="radio"] {
    @include hide-input();
    @include bp-l-less {display: none;}
  }

  input#question-funnel-1:checked ~ div.page-intro div.question-funnels-options ul li label[for="question-funnel-1"],
  input#question-funnel-2:checked ~ div.page-intro div.question-funnels-options ul li label[for="question-funnel-2"],
  input#question-funnel-3:checked ~ div.page-intro div.question-funnels-options ul li label[for="question-funnel-3"] {
    margin-top: 0;
    padding-top: calc($spacing*1.5);
    background: white;
    color: $clr-1;
    box-shadow: none;

    @include bp-xl {padding-top: calc($spacing*2);}

    &:before {display: block;}
  }

  input#question-funnel-1:checked ~ div.page-intro ~ div.question-funnels-content div.question-funnel-1,
  input#question-funnel-2:checked ~ div.page-intro ~ div.question-funnels-content div.question-funnel-2,
  input#question-funnel-3:checked ~ div.page-intro ~ div.question-funnels-content div.question-funnel-3 {display: block;}

  // page-intro

  div.page-intro {
    padding-bottom: calc($spacing*2);
    @include bp-l {padding-bottom: 0;}
  }

  // options

  div.question-funnels-options {
    // overflow-x: scroll;
    // margin: 0 (-$gutter);
    display: none;

    @include bp-l {
      display: block;
      overflow: visible;
      margin: 0;
    }

    ul {
      display: flex;
      margin: 0;
      //min-width: 768px;
      
      li {
        display: flex;
        align-items: stretch;
        flex: 1;

        label {
          position: relative;
          display: block;
          margin: 0 2px;
          margin-top: calc($spacing/2);
          padding: $spacing;
          padding-bottom: calc($spacing*1.5);
          background-color: $clr-1;
          background-image: linear-gradient(to bottom, transparent 0%, rgba(black,0.2) 96%, rgba(black,0.4) 100%);
          font-size: $fs-22;
          font-weight: 300;
          cursor: pointer;
          outline: none;
          user-select: none;
          -webkit-user-select: none;
          box-shadow: 0 -1px 4px 0 rgba(black,0.3);

          @include bp-l {border-radius: $border-radius $border-radius 0 0;}

          &.question-funnel-1 {
            background-color: $clr-b2;
            @include bp-l {&:hover {background-color: lighten($clr-b2, 3%);}}
          }

          &.question-funnel-2 {
            background-color: $clr-b6;
            @include bp-l {&:hover {background-color: lighten($clr-b6, 3%);}}       
          }

          &.question-funnel-3 {
            background-color: $clr-b3;
            @include bp-l {&:hover {background-color: lighten($clr-b3, 3%);}}
          }

          @include bp-xl {
            padding: calc($spacing*1.5) $spacing;
            font-size: $fs-25;
          }

          &:before {
            display: none;
            @extend .icon-extension, .icon-angle-down;
            position: absolute;
            left: 0; right: 0; bottom: 0;
            background: white;
            text-align: center;
            font-size: $fs-20;
            color: $clr-2;
          }
        }

        &:first-child label {margin-left: 0;}
        &:last-child label {margin-right: 0;}
      }
    }
  }

  // content

  div.question-funnels-content {
    background: white;
    text-align: left;

    div.question-funnel {
      display: none;

      h2 {
        text-align: center;
        @include bp-m {text-align: left;}
      }

      @include bp-l-less {
        &.question-funnel-1,
        &.question-funnel-2,
        &.question-funnel-3 {display: block!important;}
      }
    }
  }

  div.contact,
  div.double-testimonial {
    @include bp-l {display: none;}
  }
}










// call to actions

main.content {
  > div:last-child:is([class*="cta"]) {
    background: $clr-1;
    color: white;

    div.wrap {
      blockquote {
        div.quote {
          background: $clr-2;

          &:after {
            border-color: $clr-2 transparent transparent $clr-2;
          }

          &.single:after {
            @include bp-m {border-color: $clr-2 $clr-2 transparent transparent;}
          }
        }
      }
    }
  }
}

div.strip.cta {
  position: relative;
  z-index: 50;
  padding: calc($spacing*1.5) 0;
  background-color: $clr-3;
  box-shadow: inset 0 -2px 5px rgba(black,0.05);
  text-align: center;

  @include bp-m {
    text-align: left;
    &.download {padding: $spacing 0;}
  }

  &.client-feedback {
    padding: calc($spacing*2) 0;

    div.wrap {
      display: block;

      > h2 {
        position: relative;
        z-index: 1;
        margin-bottom: $vmargin;
        font-size: $fs-30;

        @include bp-m {
          width: $col-5;
          margin: 0 0 -2rem;
        }

        a {
          text-decoration-line: none;

          @include bp-m {
            &:hover,
            &:focus {text-decoration-line: underline;}
          }
        }
      }

      blockquote {
        margin: 0;
        
        @include bp-m {display: flex;}

        div.quote {
          position: relative;
          border-radius: $border-radius;
          padding: $spacing;
          background: darken($clr-3,5%);
          text-align: center;

          @include bp-m {
            flex: 1 1 auto;
            order: 2;
          }

          &.size-small {font-size: $fs-18;}
          &.size-medium {font-size: $fs-24; font-weight: 300;}
          &.size-large {font-size: $fs-25; font-weight: 300; @include bp-m { font-weight: 200; font-size: $fs-35;}}

          &:after {
            position: absolute;
            top: 100%; left: 50%;
            border: 10px solid;
            border-color: darken($clr-3,5%) transparent transparent darken($clr-3,5%);
            content: "";

            @include bp-m {
              top: auto; left: auto; bottom: 0.7rem; right: 100%;
              margin-left: -10px;
              border-color: darken($clr-3,5%) darken($clr-3,5%) transparent transparent;
            }
          }

          >:last-child {margin-bottom: 0;}

          p {
            &:before {content: "\0201C";}
            &:after {content: "\0201D";}
          }
        }

        div.source {
          margin-top: calc($vmargin/2);

          @include bp-m {
            flex: 0 0 auto;
            display: flex;
            align-items: flex-end;
            margin: 0;
            width: 16rem;

            &.download {width: 18rem;}
          }

          span.initials {
            display: inline-block;
            vertical-align: middle;
            margin: 0.75rem 0.75rem 0;
            border-radius: 3rem;
            width: 2.75rem;
            height: 2.75rem;
            line-height: 2.75rem;
            background: rgba(black,0.5);
            text-decoration: none;
            text-align: center;
            font-size: $fs-22;
            font-weight: 300;
            color: white;

            @include bp-m {
              flex: 0 0 auto;
              order: 2;
              margin: 0 1.4rem 0 0.75rem;
            }

            &.s2 {background: $clr-b2;}
            &.s3 {background: $clr-b3;}
            &.s4 {background: $clr-b4;}
            &.s6 {background: $clr-b6;}
          }

          p {
            vertical-align: middle;
            margin: 0.75rem 0 0;
            text-align: center;
            font-size: $fs-16;

            @include bp-s {
              display: inline-block;
              text-align: left;
            }

            @include bp-m {
              flex: 1;
              margin: 0;
              text-align: right;
            }

            span {display: block;}
          }
        }
      }
    }
  }

  &.double-testimonial {
    div.wrap {
      display: block;

      ul {
        margin-bottom: 0;

        @include bp-m-less {
          li:first-child {margin-bottom: $vmargin;}
        }

        @include bp-m {
          margin-top: -3rem;
          li {display: flex;}
        }
      }

      blockquote {
        margin: 0;

        @include bp-m {
          display: flex;
          flex-direction: column;
        }

        div.quote {
          position: relative;
          border-radius: $border-radius;
          padding: $spacing;
          background: white;
          text-align: center;
          box-shadow: 0 0 20px 5px rgba(black,0.10);

          &.size-small {font-size: $fs-18;}
          &.size-medium {font-size: $fs-24; font-weight: 300;}
          &.size-large {font-size: $fs-25; font-weight: 300; @include bp-m { font-weight: 200; font-size: $fs-35;}}

          @include bp-m {
            flex: 1 1 auto;
            display: flex;
            align-items: center;
          }

          &:after {
            position: absolute;
            top: 100%; left: 50%;
            border: 10px solid;
            border-color: white transparent transparent white;
            content: "";
          }

          >:last-child {margin-bottom: 0;}

          p {
            &:before {content: "\0201C";}
            &:after {content: "\0201D";}
          }
        }

        div.source {
          margin-top: ($vmargin);
          text-align: center;

          @include bp-m {
            flex: 0 0 auto;
          }

          span.initials {
            display: inline-block;
            vertical-align: middle;
            border-radius: 3rem;
            width: 2.75rem;
            height: 2.75rem;
            line-height: 2.75rem;
            background: rgba(black,0.5);
            text-decoration: none;
            text-align: center;
            font-size: $fs-22;
            font-weight: 300;
            color: white;

            &.s2 {background: $clr-b2;}
            &.s3 {background: $clr-b3;}
            &.s4 {background: $clr-b4;}
            &.s6 {background: $clr-b6;}
          }

          p {
            margin: 0.75rem 0 0;
            font-size: $fs-16;

            span {display: block;}
          }
        }
      }
    }
  }

  div.wrap {

    @include bp-m {
      display: flex;
      justify-content: center;
      align-items: center;
    }

    @include bp-l {align-items: flex-end;}

    > h2 {
      @include lead();
      margin: 0;

      @include bp-m {
        flex: 0 1 auto;
        align-self: center;
      }

      > span {
        font-size: $fs-20;

        @include bp-m {
          display: inline-block;
          margin-top: 0.35rem;
        }

        &:before {
          margin: 0 0.4rem 0 0.25rem;
          content: "\02014";
          @include bp-m {content: none;}
        }         

        span {
          @include bp-s {white-space: nowrap;}
        }
      }

      //a {color: white;}
    }

    > p,
    div.audio-insert {
      margin: $vmargin auto 0;

      @include bp-m {
        flex: 0 0 auto;
        align-self: center;
        margin: 0 0 0 $spacing;
      }

      a.button {
        @include bp-s-less {display: block;}
      }

      span.other {
        display: block;
        margin-top: 0.75rem;
        font-size: $fs-18;
        text-align: center;

        a {color: white;}
      }
    }

    div.audio-insert {
      audio {
        width: 100%;
        @include bp-m {width: 300px;}
      }
    }
  }

  @include bp-l {
    &.download,
    &.webinar,
    &.audio {

      body.guide & {margin-top: calc($vmargin*2);}

      span.image {
        flex: 0 0 auto;
        display: block;
        position: relative;
        width: 280px;
      
        &:before {
          position: absolute;
          left: calc(0rem - $spacing); bottom: calc(0rem - ($spacing*1.5));
          padding-top: 69%;
          width: 100%;
          background: no-repeat 50% 100%;
          background-size: 100% auto;
          content: "";
        }
      }

      div.wrap h2 {flex: 1 1 auto;}
    }

    &.download {
      span.image:before {bottom: calc(0rem - $spacing);}

      a.button {
        @include bp-m {margin: 0.75rem 0;}
      }
    }

    &.understanding-eu-mdr span.image:before {background-image: url(../../assets/images/content/cta-strips/understanding-eu-mdr.png);}
    &.risk-management span.image:before {background-image: url(../../assets/images/content/cta-strips/risk-management.png);}
    &.clinical-evaluation-report-cer-writing span.image:before {background-image: url(../../assets/images/content/cta-strips/clinical-evaluation-report-cer-writing.png);}

    &.mdready-starter-guide span.image:before {background-image: url(../../assets/images/content/cta-strips/mdready-starter-guide.png);}

    &.audio span.image:before {background-image: url(../../assets/images/content/cta-strips/non-clinical-physicians.png);}

    &.whitepaper-1 span.image:before {background-image: url(../../assets/images/content/cta-strips/whitepaper-1.png);}
    &.whitepaper-2 span.image:before {background-image: url(../../assets/images/content/cta-strips/whitepaper-2.png);}
    &.whitepaper-3 span.image:before {background-image: url(../../assets/images/content/cta-strips/whitepaper-3.png);}
    &.gspr-matcher span.image:before {background-image: url(../../assets/images/content/cta-strips/gspr-matcher.png);}
    &.webinar span.image:before {background-image: url(../../assets/images/content/cta-strips/webinar-1.png);}
  }
}




// associates

body.associates div.features-intro {
  @include bp-m-less {padding-top: 0;}
}

// sale content

div.sale-content {
  h3 {@extend .h2;}
}

// article pages

div.article-content {
  > p:nth-of-type(1):not([class]) {@include lead();}
  > ul {@extend .disc;}
  > ol {@extend .decimal;}
}

// guide pages

div.guide-content {
  background: white;

  @include bp-m {
    position: relative;
    z-index: 2;
    margin: -1.5rem 0 0;
    padding: 0 0 $vmargin;
    background: transparent;
  }

  div.aesthetic {
    @include bp-m {
      border-radius: 0.25rem;
      padding: calc($spacing*2);
      background: white;
      box-shadow: 0 0 4px 2px rgba(black,0.1);
    }

    > ul:not([class]) {@extend .disc;}
    > ol:not([class]) {@extend .decimal;}
    > :last-child {margin-bottom: 0;}

    @include bp-m-less {
      p.back-top {@include hide-screen();}
    }
  }
}

// general wrap for lists

div.general-wrap {
  overflow: hidden;
  margin-bottom: $vmargin;
  border-radius: $border-radius;

  &.spacer-up + p {@extend .spacer-up;}

  > ol,
  > ul {
    margin-bottom: 0;

    > li {
      margin-top: 4px;
      &:nth-child(1) {margin-top: 0;}
    }
  }
}

// announcement

div.announcement {
  padding: $spacing 0;
  background: $clr-warning;
  text-align: center;

  div.page-intro & {
    margin-bottom: $vmargin;
    border-radius: $border-radius;
  }

  //div.wrap > :last-child {margin-bottom: 0;}

  span.text {
    @include bp-m {display: inline-block;}

    > span {
      display: block;
      margin-bottom: calc($vmargin/2);

      @include bp-m {
        display: inline-block;
        margin-bottom: 0;
      }
    }

    .button {
      @include bp-m {margin-left: calc($spacing/2);}
    }
  }
}

// modal

div.modal {
  display: none;
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  z-index: 999;
  background: rgba(black, 0.8);
  text-align: center;

  &:target {
    display: block;

    @include bp-s {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }

  div.content {

    @include bp-s-less {
      position: fixed;
      top: 0; left: 0; right: 0; bottom: 0;
      background: white;
    }

    @include bp-s {
      margin: 0 auto;
      padding: $gutter;
      max-width: 520px;
    }

    div.text {
      position: relative;
      z-index: 2;
      padding: $spacing;
      background: white;
      font-size: $fs-18;

      @include bp-s {border-radius: $border-radius;}
    }

    p.close {
      margin-bottom: -$vmargin;

      a {
        span {
          display: block;
          position: relative;
          top: -2rem;
          z-index: 3;
        }

        @include bp-s {
          &:before {
            position: absolute;
            top: 0; left: 0; right: 0; bottom: 0;
            z-index: 1;
            content: "";
            cursor: default;
          }
        }
      }
    }
  }
}

// promotions

div.promotion {
  padding: calc($spacing/1.5) 0;
  background: $clr-b1;
  color: white;

  @include bp-m {text-align: center;}

  div.wrap > :last-child {margin-bottom: 0;}

  p strong {
    display: block;
    @include bp-m {display: inline;}
  }

  a {color: white;}
}

// notices

p.notice {
  position: relative;
  border: 3px solid $clr-4;
  border-radius: $border-radius;
  padding: 1rem;
  background: white;

  &:before {
    position: absolute;
    left: 0.8rem; top: 1.05rem;
    font-size: $fs-24;
  }

  &.error,
  &.success,
  &.warning,
  &.caution,
  &.info {padding-left: 2.5rem;}

  &.error {
    border-color: $clr-error;
    background: white;

    &:before {
      @extend .icon-extension,.icon-exclamation-circle;
      color: $clr-error;
    }
  }

  &.success {
    border-color: $clr-success;

    &:before {
      @extend .icon-extension,.icon-check-circle;
      color: $clr-success;
    }
  }

  &.offer {
    border-color: $clr-b1;
    background: rgba($clr-b1,0.05);
    text-align: center;

    &:before {content: none;}

    div.buy-details & {
      border-color: white;
      background: rgba(white,0.1);

      span {
        display: block;
        font-size: $fs-18;
      }

      b {font-size: $fs-22;}
    }
  }

  &.warning {
    border-color: $clr-warning;

    &:before {
      @extend .icon-extension,.icon-exclamation-triangle;
    }
  }

  &.caution {
    &:before {
      @extend .icon-extension,.icon-exclamation-circle;
    }
  }

  &.info {
    &:before {
      @extend .icon-extension,.icon-info-circle;
    }
  }
}

