    margin-bottom: $vmargin;
    border-bottom: 3px solid $clr-4;
    padding: 0.5rem 0;
    text-align: center;

    @include bp-m-less {display: block;}
  }

  ul, p {margin: 0.25rem 0;}

  &.end {
    margin-bottom: $vmargin;
    border-radius: $border-radius;
    padding: 0.5rem 1rem;
    background: $clr-3;

    div.guide-nav-end + & {
