    }

    ul.share-options {flex: 1 1 auto;}
  }

  div.meta {
    flex: 1 0 auto;
    margin-bottom: 0.5rem;

    @include bp-m {
      display: flex;
      align-items: center;
      margin-bottom: 0;
    }

    a.author-linkedin,
    span.team-photo {
      flex: 0 1 auto;
      display: block;
      margin: 0 auto 0.5rem;
      font-size: $fs-30;

      @include bp-m {margin: 0;}
    }
  }

  ul.share-options {flex: 0 0 auto;}

  ul.general {
    list-style: none;
    font-size: $fs-17;

    @include bp-m {
      margin: 0 0.75rem;
      text-align: left;
    }

    li a {font-weight: 700;}

    li[class*="icon-"] {
      display: flex;
      align-items: center;
      justify-content: center;

      @include bp-m {justify-content: left;}

      &:before {margin-right: 0.25rem;}

      // a {
