    a {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 2rem;
      width: 1.7rem;
      height: 1.7rem;
      background: $clr-b1;
      text-decoration: none;
      text-align: center;
      color: white;
      transition: box-shadow 0.1s;

      @include bp-m {
        &:hover,
        &:focus {
          box-shadow: inset 0 0 0 4rem rgba(black,0.1);
        }
      }

      span {@include hide-screen();}
    }
