}

// guide nav end

div.guide-nav-end {
  clear: both;
  border-radius: $border-radius $border-radius 0 0;
  border-bottom: 4px solid white;
  padding: calc($spacing/2) 0;
  background: $clr-4;

  @include bp-s-less {
    label.button {display: block;}
  }

  div.content {
    padding: 0 1rem;

    h2 {
      @extend .h5;
      margin: 0 0 0.5rem;
    }

    ul.icons {
      font-size: $fs-18;
      @include bp-m {margin: 0;}

      li {
        a,
        label.anchor {
          text-decoration-line: none;

          @include bp-m {
            &:hover,
            &:focus {text-decoration-line: underline;}
          }
        }
      }
    }
  }
}

// product cost

.product-cost {
  margin-bottom: 0.5rem;
  font-size: $fs-18;
  text-align: center;

  &.previous {
    opacity: 0.6;

    span.price span.amount {
      position: relative;
      overflow: hidden;

      &:after {
        position: absolute;
        left: 0; right: 0; top: 50%;
        height: 1px;
        background: $clr-b1;
        transform: rotate(-10deg);
        content: "";
      }
    }
  }

  span.info {
    display: block;
    font-size: $fs-15;
    opacity: 0.9;
  }

  span.price {
    position: relative;
    display: inline-block;

    span.amount {
      font-size: $fs-27;
      line-height: 1.1;
      &.checkout {font-size: $fs-40;}
    }

    span.frequency {
      position: absolute;
      left: 100%; bottom: 0;
      margin-left: 0.25rem;
      font-size: $fs-15;
      white-space: nowrap;
    }
  }
}

// discount code

div.product-summary div.content div.cta p.discount-code {
  margin-top: -0.75rem;
  font-size: $fs-16;

  b {
    display: block;
    color: $clr-b7;
  }
}

// pdf embed

div.download-sample {
  @include bp-m-less {text-align: center;}
}

div.sample-preview {
  display: flex;
  align-items: stretch;
}

div.pdf-insert {
  flex: 1;
  position: relative;
  border-bottom: 5px solid #4F5356;
  background: #4F5356;
  box-shadow: 0 0 10px 5px rgba(black,0.1);

  @-moz-document url-prefix() {
    border-bottom: 20px solid #2A2A2E;
    background: #2A2A2E;
  }
  
  @include bp-m-less {display: none;}

  object {
    position: absolute;
    top: 0; left: 0;
    border: 0;
    width: 100%;
    height: 100%;
    background: rgba(white,0.7);
  }
}

p.pdf-legacy {
  padding: calc($spacing*2);
  text-align: center;

  > span {
    display: block;
    margin-bottom: $vmargin;
  }
}


div.product-overview {
  div.row div.col {
    display: flex;
    align-items: center;

    > :first-child {flex: 1;}

    div.video-cta {
      overflow: hidden;
      border-radius: $border-radius;
      background: $clr-4;

      @include bp-m-less {
        margin-bottom: $vmargin;
        div.video-insert {margin: 0;}
      }

      div.video-wrap {margin-bottom: 0;}
      
      p {
        margin: 0 $spacing $vmargin;
        &.cta-lead {margin-bottom: calc($vmargin/2);}
      }
    }

    ul.styled {
      @include bp-m {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-bottom: 0;

        li {margin: 0.25rem 0;}
      }
    }
  }
}

// videos

div.video-wrap {
  margin-bottom: $vmargin;

  .box & {margin-bottom: 0;}

  &.rounded {
    overflow: hidden;
    border-radius: $border-radius;
  }

  div.video-insert,
  p.video-caption {
    //margin-left: (-$gutter); margin-right: (-$gutter);

    @include bp-m {
      margin: 0 auto;
      max-width: 600px;

      article & {
        float: right;
        clear: both;
        position: relative;
        z-index: 1;
        margin-left: $spacing;
        width: 50%;
      }

      &.playing,
      &.playing + p.video-caption {
        float: none;
        margin-left: 0;
        width: auto;
        max-width: none;
      }
    }
  }

  div.video-insert {
    position: relative;

    &.playing {
      &:before {
        display: block;
        padding-top: 56.23%;
        content: "";
      }
    }

    // &.playing,
    // a.cta {
    //   background: black;
    // }

    a.cta {
      position: relative;
      display: block;
      transition: filter 0.2s;

      @include bp-m {
        &:hover,
        &:focus {
          filter: brightness(90%);

          &:before {
            background-size: 15%;
            opacity: 1;
          }
        }
      }

      &:before {
        position: relative;
        z-index: 2;
        display: block;
        padding-top: 56.23%;
        background: url(../../assets/images/content/other/play.png) no-repeat 50%;
        background-size: 10%;
        cursor: pointer;
        content: "";
        opacity: 0.75;
        transition: background-size 0.1s, opacity 0.1s;
      }

      img {
        position: absolute;
        right: 0; top: 0; left: 0; bottom: 0;
        z-index: 1;
        width: 100%;
        height: 100%;
        cursor: pointer;
      }
    }

    object,
    video,
    iframe {
      position: absolute;
      top: 0; left: 0;
      border: 0;
      width: 100%;
      height: 100%;
      background: transparent;
    }
  }

  p.video-caption {
    margin-bottom: $vmargin;
    border-radius: 0 0 0.5rem 0.5rem;
    padding: 0.5rem 1rem;
    background: $clr-4;
    font-size: $fs-16;
  }
}

// contact button

input.contact-button-toggle {
  @include hide-input();

  &:checked + div.contact-button {

    label.cta {
      position: static;

      span.button {
        box-shadow: inset 0 1px 2px rgba(black,0.75), 0 0 0 5px rgba(white,0.1);
        &:before {@extend .icon-times;}
      }

      &:after {@include bp-s {content: "";}}
    }

    div.form {
      top: 0; right: 0;

      @include bp-s-less {left: 0;}

      @include bp-s {
        top: auto;
        margin-bottom: 5rem;
        opacity: 1;
        transition: margin-bottom 0.25s, opacity 0.25s;
      }
    }
  }
}

div.contact-button {
  position: fixed;
  bottom: 0; right: 0;
  z-index: 1000;
  padding-bottom: 1rem;

  label.cta {
    cursor: pointer;

    body.guide & {@include hide-label();}
    @include bp-s-less {@include hide-label();}

    span.button {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      z-index: 2;
      margin-right: 1rem;
      border-radius: 3rem;
      padding: 0;
      width: 2.5rem;
      height: 2.5rem;

      &:before {
        margin: 0;
        font-size: $fs-25;
      }

      @include bp-m {
        width: 3rem;
        height: 3rem;

        &:before {font-size: $fs-30;}
      }

      span {@include hide-screen();}
    }

    &:after {
      position: fixed;
      left: 0; right: 0; bottom: 0; top: 0;
      z-index: 1;
      background: rgba(black,0.3);
    }
  }

  div.form {
    position: fixed;
    bottom: 0; right: 1000em;
    z-index: 1;
    background: $clr-4;

    @include bp-s {
      margin: 0 1rem 4rem 0;
      border-radius: $border-radius;
      width: 16rem;
      background: none;
      opacity: 0;
      box-shadow: 0.25rem 0.25rem 1.5rem rgba(black,0.3);
      transition: margin-bottom 0.25s, opacity 0.25s, right 0s 0.25s;
    }

    h2 {
      @extend .h3;
      position: relative;
      margin: 0;
      background: $clr-1;
      color: white;
      padding: 0.5rem 1rem;

      @include bp-s {border-radius: $border-radius $border-radius 0 0;}

      &:after {
        position: absolute;
        right: 1rem;
        top: 0;
        border-radius: 3rem;
        width: 3rem;
        height: 3rem;
        background: white url(../../assets/images/content/other/contact-button-person.jpg) no-repeat 50%;
        background-size: cover;
        box-shadow: 0 0 0 4px white, 0 3px 8px 3px rgba(black,0.3);
        content: "";

        @include bp-s {top: -0.15rem;}
      }
    }

    form {
      padding: 1rem;
      background: $clr-4;

      @include bp-s {border-radius: 0 0 $border-radius $border-radius;}

      div.form-group {
        margin: 0 0 calc($vmargin/2);

        &.linear-fields {
          margin-bottom: 0;

          label {
            margin-top: 0;
            margin-bottom: calc($vmargin/2);
          }
        }

        input:not([type=checkbox]):not([type=radio]):not([type=submit]):not([type=file]),
        textarea,
        .custom-select {
          padding: 0.4rem 0.5rem;
          font-size: $fs-18;
        }
      }

      button {
        width: 100%;
        font-size: $fs-20;
      }
    }
  }
}

// service breakdown - landing service pages

div.service-breakdown {
  position: relative;
  z-index: 1;

  @include bp-m-less {
    > div.wrap > div:first-of-type h2:before {content: none;}
  }

  div.detail {
    margin-bottom: calc($vmargin*2);

    @include bp-m {
      div.row {align-items: center;}
    }

    h2 {
      position: relative;
      text-align: center;

      @include bp-m {text-align: left;}
      @include bp-l {span {display: inline-block; white-space: nowrap;}}

      &:before {
        display: block;
        margin: $vmargin auto;
        border-radius: 2px;
        width: 5rem;
        height: 5px;
        background: $clr-b1;
        content: "";

        @include bp-m {
          position: absolute;
          top: 0; bottom: 0; right: 100%;
          margin: 0 1rem 0 0;
          width: 5px;
          height: auto;
        }
      }
    }

    ul {margin-bottom: calc($vmargin/1.5);}
    a.single-link {font-size: $fs-18;}

    div.cta-box {
      border-radius: $border-radius;
      padding: $spacing;
      background: $clr-4;
      color: $clr-1;
      box-shadow: 0 0 50px 10px rgba(black,0.25);

      @include bp-m-less {margin-top: calc($vmargin*2);}

      p {
        text-align: center;

        &.intro {
          font-size: $fs-23;
          font-weight: 300;
        }

        &.small-text {margin: calc($vmargin/2) 0 0;}
      }

      div.general-wrap {margin: 0;}
    }

    figure.circle-image {
      overflow: hidden;
      border-radius: 50%;
      border: 5px solid $clr-4;
      max-width: 350px;
      box-shadow: 0 0 50px 10px rgba(black,0.25);

      @include bp-m-less {@include hide-screen();}
    }
  }
}

// medical professional on about page

div.med-pro {
  position: relative;
  overflow: hidden;
  margin-bottom: $vmargin;
  border-radius: $border-radius;
  background: $clr-4;
  text-align: left;

  @include bp-m {text-align: right;}

  &:before {
    display: block;
    height: 15rem;
    background: $clr-3 url(../../assets/images/content/other/medical-professionals.jpg) no-repeat 50%;
    background-size: cover;
    content: "";

    @include bp-m {
      position: absolute;
      top: 0; left: 0; bottom: 0;
      border-right: 4px solid white;
      width: 50%;
      height: auto;
    }
  }

  p {
    @include lead();
    margin-bottom: 0;
    padding: 1rem;
    text-align: left;

    @include bp-s {padding: 2rem;}
    
    @include bp-m {
      display: inline-block;
      padding: 4rem 2rem;
      width: 50%;
    }
  }
}

