// testimonials

div.testimonials {
  margin: $vmargin 0;

  ul {
    margin-bottom: 0;
    text-align: center;

    li {

      input {
        @include hide-input();

        &:checked {
          + div.content {
            overflow: visible;
            //visibility: visible;
            margin-left: 0;
            padding: 1.25rem 0;
            width: 100%;
            max-height: 30rem;
            opacity: 1;
            transition: opacity 0.25s 0.25s, margin-left 0.25s 0.25s, max-height 0s 0.25s, padding 0s 0.25s;

            @include bp-m {padding: 3rem 0;}
          }
        }
      }

      div.content {
        display: flex;
        justify-content: center;
        overflow: hidden;
        //visibility: hidden; - don't add this because it causes Google Lighthouse to complain about hidden labels
        margin-left: -20rem;
        padding: 0;
        width: 0;
        max-height: 0;
        opacity: 0;
        transition: opacity 0.25s, margin-left 0.25s, visibility 0s 0.25s, width 0s 0.25s, max-height 0s 0.25s, padding 0s 0.25s;

        blockquote {
          position: relative;
          display: flex;
          flex-direction: column;
          justify-content: center;
          margin-bottom: 0;
          border-left: 5px solid $clr-b1;
          border-radius: $border-radius;
          padding: $spacing calc($spacing/1.7);
          max-width: 750px;
          min-height: 18rem;
          background: $clr-4;

          .bg-b5 & {background: rgba($clr-1,0.05);}

          @include bp-s {
            padding: $spacing;
            min-height: 22rem;
          }

          @include bp-m {padding: $spacing calc($spacing*1.5);}

          div.quote {
            font-size: $fs-18;
            @include bp-m {font-size: $fs-20;}

            &.small {
              @include bp-s-less {
                p:last-child {@include hide-screen();}
              }
            }

            &.medium {
              font-size: $fs-22;
              @include bp-m {font-size: $fs-25;}
            }
             
            &.large {
              font-size: $fs-25;
              font-weight: 300;

              @include bp-s {font-size: $fs-30;}

              @include bp-m {
                font-size: $fs-40;
                font-weight: 200;
              }
            }
              
            p {
              &:before {content: "\0201C";}
              &:after {content: "\0201D";}
            }
          }

          p.source {
            margin-bottom: 0;
            font-style: normal;
            font-weight: 700;
            @include bp-m-less {font-size: $fs-18;}
            span {display: block;}
          }
        }

        label.button {
          display: flex;
          align-items: center;
          justify-content: center;
          position: absolute;
          right: 50%; top: 100%;
          z-index: 10;
          margin: -1.25rem -1.25rem 0 0;
          border-radius: 3rem;
          padding: 0;
          width: 2.5rem;
          height: 2.5rem;
          font-size: $fs-25;

          @include bp-m {
            right: 0; top: 50%;
            margin: -1.25rem -1.25rem 0 0;
          }

          &:before {
            @extend .icon-extension, .icon-angle-right;
          }

          span {@include hide-label();}
        }
      }
    }
  }
}

